<template>
  <div class="login">
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span>通用后台管理系统</span>
      </div>
      <el-form label-width="80px" :model="form" ref="form" :rules="rules">
        <el-form-item label="用户名" prop="username">
          <el-input v-model="form.username"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password">
          <el-input type="password" v-model="form.password"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="login('form')">登录</el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>

<script>
import { setToken } from "@/utils/setToken.js";
import { login } from "@/api/api.js";
import axios from "axios";

export default {
  name: "LoginView",
  data() {
    const validateName = (rule, value, callback) => {
      // 验证用户名
      // 4-10位字母或数字
      let reg = /(^[a-zA-Z0-9]{4,10}$)/;
      if (value === "") {
        callback(new Error("请输入用户名"));
      } else if (!reg.test(value)) {
        callback(new Error("请输入4-10位用户名"));
      } else {
        callback();
      }
    };
    const validatePass = (rule, value, callback) => {
      // 验证密码
      // 6-12位字母或数字  // 暂时不写特殊字符
      let pass =
        /^\S*(?=\S{6,12})(?=\S*\d)(?=\S*[A-Z])(?=\S*[a-z])(?=\S*[!@#$%^&*? ])\S*$/;
      if (value === "") {
        callback(new Error("请输入密码"));
      } else if (!pass.test(value)) {
        callback(new Error("密码为6-14位字母或数字"));
      } else {
        callback();
      }
    };
    return {
      form: {
        username: "",
        password: "",
      },
      rules: {
        username: [{ validator: validateName, trigger: "blur" }],
        password: [{ validator: validatePass, trigger: "blur" }],
      },
    };
  },
  methods: {
    login(form) {
      // 这里的form是字符串，不是对象
      this.$refs[form].validate((valid) => {
        // 格式校验固定写法
        if (valid) {
          console.log(this.form);
          // 登录逻辑
          // 请求登录接口 有后端编写接口
          //#region 登陆逻辑版本一
          // axios
          //   .post("http://localhost:8080/api/login", this.form)
          //   .then((res) => {
          //     console.log(res);
          //     if (res.data.status === 200) {
          //       // 登录成功，保存用户名到本地存储
          //       // setToken("username", res.data.username);
          //       // 登陆成功后跳转到首页
          //       this.$message({
          //         message: res.data.message,
          //         type: "success",
          //       });
          //       this.$router.push("/home");
          //     }
          //   })
          //   .catch((err) => {
          //     console.error(err);
          //   });
          //#endregion
          //#region 登陆逻辑版本二
          // this.service
          //     .post("/login", this.form)
          //     .then((res) => {
          //         if (res.data.status === 200) {
          //             // 登录成功，保存用户名到本地存储
          //             setToken("username", res.data.username);
          //             // 保存 token 到本地存储
          //             setToken("token", res.data.token);
          //             // 登陆成功后跳转到首页
          //             this.$message({
          //                 message: res.data.message,
          //                 type: "success",
          //             });
          //             this.$router.push("/home");
          //         }
          //         console.log(res);
          //     })
          //     .catch((err) => {
          //         console.error(err);
          //     });
          //#endregion
          //#region 登陆逻辑版本三
          login(this.form)
            .then((res) => {
              if (res.data.status === 200) {
                // 登录成功，保存用户名到本地存储
                setToken("username", res.data.username);
                // 保存 token 到本地存储
                setToken("token", res.data.token);
                // 登陆成功后跳转到首页
                this.$message({
                  message: res.data.message,
                  type: "success",
                });
                this.$router.push("/home");
              }
              console.log(res);
            })
            .catch((err) => {
              console.error(err);
            });
          //#endregion
        } else {
          // console.log(this);
          console.log(this.form.password);
        }
      });
    },
  },
};
</script>

<style lang="scss">
.login {
  width: 100%;
  height: 100%;
  position: absolute;
  background: url("@/assets/img/background-1.webp") no-repeat center center;
  background-size: cover;

  .el-card {
    background: #65768557;
  }

  .box-card {
    width: 450px;
    margin: 200px auto;
    color: white;

    .el-form .el-form-item__label {
      color: white;
    }

    .el-card__header {
      font-size: 34px;
    }

    .el-button {
      width: 100%;
    }
  }
}
</style>
